let sreenWidth = window.screen.width
const cargo = document.querySelector('.typesCargo__swiper')
const certificates = document.querySelector('.certificates__swiper')
let swiperCargo, swiperCertificates


window.addEventListener('resize', () => {
  sreenWidth = window.screen.width
  initSwiper(sreenWidth)
})

function initSwiper(sreenWidth) {
  if (sreenWidth < 479) {
    document.body.style.paddingBottom = navigation.clientHeight + 'px'
  } else {
    document.body.style.paddingBottom = null
  }

  if (sreenWidth < 998) {
    if (cargo.classList.contains('swiper-init')) return
    cargo.classList.add('swiper-init')
    swiperCargo = new Swiper('.typesCargo__swiper', {
      navigation: {
        nextEl: '.typesCargo__swiper-button-next',
        prevEl: '.typesCargo__swiper-button-prev',
      },
      breakpoints: {
        320: {
          slidesPerView: 1,
        },
        480: {
          slidesPerView: 2,
        },
        640: {
          slidesPerView: 3,
        }
      }
    })
  } else {
    if (swiperCargo) {
      swiperCargo.destroy(true, true);
      cargo.classList.remove('swiper-init')
    }
  }

  if (sreenWidth < 1100) {
    if (certificates.classList.contains('swiper-init')) return

    certificates.classList.add('swiper-init')
    swiperCertificates = new Swiper('.certificates__swiper', {
      navigation: {
        nextEl: '.certificates__swiper-button-next',
        prevEl: '.certificates__swiper-button-prev',
      },
      pagination: {
        el: '.certificates__swiper-pagination',
      },
      slidesPerView: 1,
      spaceBetween: 20,
      centeredSlides: true
    })
  } else {
    if (swiperCertificates) {
      swiperCertificates.destroy(true, true);
      certificates.classList.remove('swiper-init')
    }
  }
}

initSwiper(sreenWidth)